  font-weight: 500;
  line-height: 14px;
  letter-spacing: 0.28px;
  text-transform: uppercase;
  color: #316dc2;
}
.menu-button__icon-wrapper {
  width: 46px;
  height: 46px;
  border-radius: 50%;
  background-color: #316dc2;
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu-button__icon-cross {
  display: none;
}
.menu-button:hover, .menu-button:focus {
  background-color: #316dc2;
}
.menu-button:hover .menu-button__text, .menu-button:focus .menu-button__text {
  color: #ffffff;
}
.menu-button:hover .menu-button__icon-wrapper, .menu-button:focus .menu-button__icon-wrapper {
  background-color: #ffffff;
}
.menu-button:hover .menu-button__icon-wrapper svg, .menu-button:focus .menu-button__icon-wrapper svg {
  stroke: #316dc2;
}
.menu-button:active {
  background-color: #316dc2;
}
.menu-button:active .menu-button__text {
  color: rgba(255, 255, 255, 0.7);
}
.menu-button:active .menu-button__icon-wrapper {
  background-color: #ffffff;
}
.menu-button:active .menu-button__icon-wrapper svg {
  stroke: rgba(49, 109, 194, 0.7);
}
.menu-button:disabled {
  background-color: rgba(255, 255, 255, 0);
  border-color: #a9afb7;
  cursor: default;
}
.menu-button:disabled .menu-button__text {
  color: #a9afb7;
}
.menu-button:disabled .menu-button__icon-wrapper {
  background-color: #a9afb7;
}
.menu-button:disabled .menu-button__icon-wrapper svg {
  stroke: rgba(255, 255, 255, 0.6);
}
.menu-button--opened {
  min-width: 286px;
  background-color: #316dc2;
}
.menu-button--opened .menu-button__text {
  color: #ffffff;
}
.menu-button--opened .menu-button__icon-wrapper {
  background-color: #ffffff;
}
.menu-button--opened .menu-button__icon-wrapper svg {
  stroke: #316dc2;
}

.modal-menu {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
  /*   &__close-btn {
      position: absolute;
      top: 22px;
      right: 22px;
      z-index: 1;

      width: 40px;
      height: 40px;
      padding: 0;

      background: $color-transparent;
      border: none;
      cursor: pointer;
    }

    &--responsive {
      .modal__content {
        width: 100%;
      }
    }

    &--fit-content {
      .modal__content {
        width: auto;

        @include vp-767 {
          width: 100%;
        }
      }
    }

    &--no-scale {
      transform: none;
    } */
}
.modal-menu::-webkit-scrollbar {
  display: none;
}
.modal-menu__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: flex-end;
  align-items: flex-start;
  width: 100%;
  min-height: 100%;
  padding: 82px 100px;
  max-width: 1440px;
  margin: 0 auto;
}
.modal-menu__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal-menu__content {
  position: relative;
  min-width: 286px;
  padding: 35px 25px;
  background-color: #316dc2;
  border-radius: 16px;
  overflow: hidden;
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  transition: clip-path 0.3s ease-in-out, height 0.3s linear;
}
@media (max-width: 767px) {
  .modal-menu__content {
    width: 100%;
    padding: 20px;
  }
}
.modal-menu__list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 28px;
}
.modal-menu__link {
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  letter-spacing: 0.32px;
  text-transform: uppercase;
  color: rgba(255, 255, 255, 0.7);
  transition: 0.3s linear;
}
.modal-menu__link--current {
  color: #ffffff;
}
.modal-menu__link:hover, .modal-menu__link:focus {
  color: #ffffff;
}
.modal-menu__sub-menu {
  list-style-type: none;
  margin: 0;
  padding: 16px 0 0 16px;
  display: flex;
  flex-direction: column;
  gap: 16px;
  overflow: hidden;
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
  transition: clip-path 0.3s ease-in-out, height 0.3s linear;
  height: auto;
}
.modal-menu__sub-menu--closed {
  clip-path: polygon(0 0, 100% 0, 100% 0, 0 0);
  height: 0;
}
.modal-menu__sub-link {
  font-size: 16px;
  font-weight: 400;
  line-height: 16px;
  transition: 0.3s linear;
  color: rgba(255, 255, 255, 0.6);
}
.modal-menu__sub-link--current {
  color: rgba(255, 255, 255, 0.9);
}
.modal-menu__sub-link:hover, .modal-menu__sub-link:focus {
  color: rgba(255, 255, 255, 0.9);
}
.modal-menu__sub-button {
  margin: 0 0 0 10px;
  padding: 0;
  border: 0;
  background: 0;
}
.modal-menu__sub-button svg {
  transition: 0.3s linear;
}
.modal-menu__sub-button--rotated {
  transform: rotate(180deg);
}
.modal-menu__sub-button:hover svg, .modal-menu__sub-button:focus svg {
  stroke: #ffffff;
}
.modal-menu--preload {
  transition: none;
}
.modal-menu.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}
.modal-menu.is-active .modal-menu__content {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 100%);
}

.main-footer {
  padding: 20px 0 50px;
  background-color: #f0efe9;
}
@media (max-width: 767px) {
  .main-footer {
    padding: 20px 0 40px;
  }
}

.main-footer__container {
  display: flex;
  gap: 25px;
  justify-content: space-between;
  flex-wrap: wrap;
}
@media (max-width: 767px) {
  .main-footer__container {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
  }
}

.main-footer__nav-wrapper {
  display: flex;
  gap: 52px;
  align-items: center;
  max-width: calc(100% - 200px);
}
@media (max-width: 1199px) {
  .main-footer__nav-wrapper {
    gap: 12px;
    justify-content: space-between;
    flex-grow: 1;
    width: 100%;
    max-width: none;
  }
}
@media (max-width: 767px) {
  .main-footer__nav-wrapper {
    flex-direction: column;
    justify-content: flex-start;
    width: unset;
  }
}

.main-footer__logo {
  display: block;
}

.main-footer__menu-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 30px;
  align-items: center;
}
@media (max-width: 1199px) {
  .main-footer__menu-list {
    gap: 15px;
  }
}
@media (max-width: 767px) {
  .main-footer__menu-list {
    justify-content: center;
    column-gap: 30px;
    row-gap: 20px;
  }
}

.main-footer__menu-link {
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  transition: 0.3s linear;
}
.main-footer__menu-link:hover, .main-footer__menu-link:focus {
  color: #316dc2;
}
.main-footer__menu-link:active {
  color: #275699;
}
@media (max-width: 1199px) {
  .main-footer__menu-link {
    line-height: 20px;
  }
}
@media (max-width: 767px) {
  .main-footer__menu-link {
    font-size: 14px;
  }
}

.main-footer__social-list {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  align-items: center;
  max-width: 148px;
}
@media (max-width: 1199px) {
  .main-footer__social-list {
    gap: 10px;
    order: 1;
  }
}
@media (max-width: 767px) {
  .main-footer__social-list {
    order: 1;
  }
}

.main-footer__social-link {
  width: 36px;
  height: 36px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #316dc2;
  border-radius: 50%;
  transition: 0.3s linear;
}
.main-footer__social-link:hover svg, .main-footer__social-link:focus svg {
  fill: rgba(255, 255, 255, 0.7);
}
.main-footer__social-link:active {
  color: #275699;
}
.main-footer__social-link:active svg {
  fill: #ffffff;
}
@media (max-width: 767px) {
  .main-footer__social-link {
    width: 26px;
    height: 26px;
  }
  .main-footer__social-link svg {
    width: 12px;
    height: 12px;
  }
}

.main-footer__info-wrapper {
  flex-grow: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
@media (max-width: 1199px) {
  .main-footer__info-wrapper {
    flex-grow: 0;
    flex-direction: column-reverse;
    gap: 10px;
    width: unset;
  }
}

.main-footer__copyright {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}
@media (max-width: 1199px) {
  .main-footer__copyright {
    font-size: 12px;
    line-height: 12px;
    opacity: 0.6;
  }
}
@media (max-width: 767px) {
  .main-footer__copyright {
    line-height: 14px;
  }
}

.main-footer__policy {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  position: relative;
}
.main-footer__policy::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  background-color: #484848;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  transition: 0.3s linear;
}
.main-footer__policy:hover, .main-footer__policy:focus {
  color: #316dc2;
}
.main-footer__policy:hover::after, .main-footer__policy:focus::after {
  background-color: #316dc2;
}
.main-footer__policy:active {
  color: #275699;
}
.main-footer__policy:active::after {
  background-color: #275699;
}
@media (max-width: 1199px) {
  .main-footer__policy {
    font-size: 12px;
    line-height: 12px;
  }
}
@media (max-width: 767px) {
  .main-footer__policy {
    line-height: 14px;
  }
}

